.card {
    width: 250px;
    height: 110px;
    border: none;
    background-color: #fff;
    box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.2);
    height: auto;
    @media(max-width: 960px) {
        margin: 15px auto; } }
.card-body {
    padding: 1rem; }
.card-titulo {
    text-align: center;
    font-size: 1em; }
.card-numero {
    font-size: 1.7em;
    text-align: center;
    margin-block-end: 0;
    line-height: .7; }
.card-subnumero {
    font-size: .5em;
    margin-block-start: 0; }
.suspect {
    .card-numero {
        color:	#347aac; } }
.confirm {
    .card-numero {
        color: #f49435; } }
.data-title {
    font-size: .8em;
    margin-block-end: 0;
    margin-block-start: 0;
    text-align: center;
    @media(max-width: 650px) {
        font-size: .6em; } }
.data-date {
    font-size: .8em;
    margin-block-end: 0;
    margin-block-start: 0;
    border-top: 1px solid #ccc;
    text-align: center;
    @media(max-width: 650px) {
        font-size: .6em; } }
.panel-div {
    margin: 20px auto 20px auto; }
.data-panel {
    text-align: center;
    font-size: .7em;
    margin-block-end: 0;
    margin-block-start: 0;
    color: #fff;
    margin-bottom: 5px;
    border-radius: 4px;
    @media(max-width: 650px) {
        font-size: .5em; } }
.dp-c {
    background-color: #f49435; }
.dp-s {
    background-color: 	#347aac; }
.dp-d {
    background-color: #333; }
